@import "../../styles/index.scss";

.app-wrapper {
  @include borders(white, #999);
  background: #c2c2c2;
  padding: 16px;
}

.header {
  @include borders(#7b7b7b, white);
  background: #c0c0c0;
  padding: 10px 12px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.body-side {
  @include borders(#7b7b7b, white);
  margin-top: 16px;
  display: grid;
  grid-template-rows: repeat(9, 1fr);
  grid-template-columns: repeat(9, 1fr);
}

.face {
  @include borders(white, #999);
  @include buttons;
  font-size: 35px;
  width: 52px;
  height: 54px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
